@import '../../../styles/variables.module';
.newCollectionBlock {
  max-height: 84.1rem;
  position: relative;

  .newCollectionBlock__image {
    display: block;
    width: 100%;
    position: relative;
    img {
      width: 100%;
    }

    &::after {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 1px;
      background: linear-gradient(
                      90.08deg,
                      rgba(222, 222, 222, 0.54) 14.47%,
                      rgba(225, 225, 225, 0) 45.58%
      );
    }
  }
}
.newCollectionBlock__section {
  display: flex;
  max-width: 57.8rem;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(0, -50%);
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 4rem;
  padding-left: 8rem;

  .newCollectionBlock__title1 {
    color: $black;
    word-wrap: break-word;
    @include headline-1();
    font-weight: 800;
  }

  .newCollectionBlock__text {
    max-width: 57.8rem;
    color: $black;
    @include body-2();
    word-wrap: break-word;
    text-align: start;
  }

  .newCollectionBlock__button {
    @include button(19.5rem);
    @include buttonBlack();
  }
}
@media (min-width: 768px) and (max-width: 1280px) {
  .newCollectionBlock {
    max-height: 91.7rem;
  }
  .newCollectionBlock__section {
    gap: 2.4rem;
    padding-left: 3.2rem;
    .newCollectionBlock__title1 {
      @include tablet-headline-1();
      font-weight: 800;
      max-width: 42.4rem;
    }
    .newCollectionBlock__text {
      max-width: 44rem;
    }
  }
}
@media (max-width: 767px) {
  .newCollectionBlock {
    .newCollectionBlock__image {
      width: 100%;
      img {
        width: 100%;
      }
    }
    .newCollectionBlock__section {
      gap: 2.4rem;
      padding-left: 1.6rem;

      .newCollectionBlock__title1 {
        @include tablet-headline-32();
        max-width: 25rem;
      }

      .newCollectionBlock__text {
        max-width: 28.5rem;
        @include body-4();
      }

      .newCollectionBlock__button {
        width: 18rem;
        height: 4.8rem;
        padding: 1.2rem;
        span {
          @include button-s();
        }
      }
    }
  }
}
